package wizard

import wizard.files.*
import wizard.files.convention.ConventionPluginsBuildGradleKts
import wizard.files.convention.ConventionPublicationGradleKts
import wizard.files.lib.*

fun ProjectInfo.buildFiles() = buildList {
    add(Gitignore())
    add(Readme(this@buildFiles))

    add(GradleBat())
    add(Gradlew())
    add(GradleWrapperProperties(this@buildFiles))
    add(GradleWrapperJar())
    add(GradleLibsVersion(this@buildFiles))

    add(GradleProperties())
    add(RootBuildGradleKts(this@buildFiles))
    add(SettingsGradleKts(this@buildFiles))

    add(ConventionPluginsBuildGradleKts(this@buildFiles))
    add(ConventionPublicationGradleKts(this@buildFiles))

    add(ModuleBuildGradleKts(this@buildFiles))

    add(FibonacciKt(this@buildFiles))
    add(FibonacciTestKt(this@buildFiles))
}